$primary-color:#Fdff83;
$self_color:#d4cced;
$future_color:#30e3df;

.company_register_wrap{
    background-color: #baf5f4;
    .register_title{
        font-size: 90px;
        text-shadow: 0 15px 4px rgba(0,0,0,0.25);
        position: relative;
        font-family: fantasy;
    }
    .company_title{
        font-size: 30px;
    }
    .register_form_wrap{
        background-color: #fff;
        border-radius: 30px;
        position: relative;
        width: 65%;
        margin: 0 auto;
        padding: 10px 25px;
    }
    .file_wrap{
        display: block;
    }
    .form-control{
        border-radius: 30px;
    }
    .btn{
        width: 30%;
        background-color: $future_color;
        box-shadow: 0px 4px 4px #27e9d1;
        border-radius: 30px;
        border: none;
        color: #000;
    }
    .btn:active{
        box-shadow: none;
        transform: translateY(4px);
        background-color: $future_color !important;
        border-color: $future_color !important;
        border: none;
        color: #000 !important;
    }
    a:hover{
        text-decoration: underline !important;
    }
}